<template>
  <!--  -->
  <div id="TagImage">
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      width="30%"
      :before-close="cancel"
      ><el-autocomplete
        class="inline-input"
        v-model="tagName"
        :fetch-suggestions="querySearch"
        placeholder=""
        @select="handleSelect"
      ></el-autocomplete>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="determine"> </el-button>
      </span>
    </el-dialog>
    <!--   -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible2"
      :close-on-click-modal="false"
      width="30%"
    >
      <el-tag
        :key="tag"
        v-for="tag in dynamicTags"
        closable
        :disable-transitions="false"
        @close="handleClose(tag)"
      >
        {{ tag }}
      </el-tag>
      <el-input
        class="input-new-tag"
        v-if="inputVisible"
        v-model="inputValue"
        ref="saveTagInput"
        size="small"
        @keyup.enter.native="handleInputConfirm"
        @blur="handleInputConfirm"
      >
      </el-input>
      <el-button v-else class="button-new-tag" size="small" @click="showInput"
        >+ New Tag</el-button
      >

      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelManage"> </el-button>
        <el-button type="primary" @click="determineManage"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-row>
      <el-col :span="24"
        ><div class="grid-content bg-purple-dark" id="first">
          <el-button type="primary" @click="lastPage"></el-button>

          <span>:{{ paramList.dataSetName }}</span>
        </div></el-col
      >
    </el-row>
    <!--  -->
    <!-- <el-row>
      <el-col :span="24"
        ><div
          class="grid-content bg-purple"
          id="second"
          v-show="isTaged == false"
        >
          <el-button @click="startTag" class="startTag untrainedBtn"
            ></el-button
          >
          <el-button @click="stopTag" class="stopTag untrainedBtn"
            ></el-button
          >
          <el-button @click="unTarget" class="untrainedBtn"></el-button>
          <el-button @click="removeOne" class="removeOne untrainedBtn"
            ></el-button
          >
          <el-button @click="managerTag" class="managerTag untrainedBtn"
            ></el-button
          >
          <el-button @click="saveData" class="managerTag untrainedBtn"
            ></el-button
          >
        </div>
        <div
          class="grid-content bg-purple"
          id="second"
          v-show="isTaged == true"
        >
          <el-button
            @click="removeTag(curImage, curImageIndex)"
            class="removeOne untrainedBtn"
            ></el-button
          >
        </div>
      </el-col>
    </el-row> -->
    <!--  -->
    <el-row class="el_row_three">
      <!--  -->
      <el-col class="noEmpty" :span="4">
        <div id="TagManage">
          <!--  -->
          <div class="title"></div>
          <!--  -->
          <div class="btnList">
            <div
              class="btnItem"
              :style="isTaged ? '' : 'background:#85CE61;'"
              @click="setIsTaged(false)"
            >
              
            </div>
            <div
              class="btnItem"
              :style="isTaged ? 'background:#85CE61;' : ''"
              @click="setIsTaged(true)"
            >
              
            </div>
          </div>
          <!--  -->
          <div class="imgBtnList" @click="selectImg($event.target.dataset)">
            <div class="listEmpty" v-show="total_pics == 0">
              <div>
                <img src="~assets/images/kong.png" alt="" />
                <div></div>
              </div>
            </div>
            <div
              class="imgBtn"
              v-for="(value, index) in isTagImgList"
              :key="value.imagename"
              :data-index="index"
            >
              <el-image :src="value.image" :data-index="index"></el-image>
              <!-- Array.isArray(value['labelBoxes']) || value.unTag -->
              <div
                class="imgTag"
                v-show="Array.isArray(value['labelBoxes']) || value.unTag"
              >
                
              </div>
              <!-- <div
                class="imgTag"
                v-show="
                  Array.isArray(value['labelBoxes']) &&
                  value['labelBoxes'].length == 0 &&
                  value.unTag
                "
              >
                
              </div> -->
            </div>
          </div>
          <!--  -->
          <div class="pageBtn">
            <el-button
              :disabled="
                isNextPage == 'unLast' || isNextPage == 'unAll' ? true : false
              "
              @click="getPage(-1)"
              ></el-button
            >
            <el-button
              :disabled="
                isNextPage == 'unNext' || isNextPage == 'unAll' ? true : false
              "
              @click="getPage(1)"
              ></el-button
            >
          </div>
        </div>
      </el-col>
      <!--  -->
      <el-col class="noEmpty" :span="16">
        <!-- <div class="Empty" v-show="total_pics == 0"> -->
        <!--  -->
        <div class="Empty" v-show="total_pics == 0">
          <div>
            <img src="~assets/images/kong.png" alt="" />
            <div></div>
          </div>
        </div>
        <!--  -->
        <div id="total">
          <div class="command">
            <div v-if="isTaged == false" class="btns">
              <span class="iconfont icon-kuangxuan1" @click="startTag">
                <i class="tip"></i>
              </span>
              <span class="iconfont icon-wancheng-copy" @click="saveData"
                ><i class="tip"></i></span
              >
              <span
                class="icon iconfont icon-icon_common_region"
                @click="unTarget"
                ><i class="tips"></i></span
              >
              <span class="icon iconfont icon-shanchu" @click="removeOne"
                ><i class="tips"></i>
              </span>
            </div>
            <div v-if="isTaged == true" class="btns">
              <span
                class="icon iconfont icon-shanchu"
                @click="removeTag(curImage, curImageIndex)"
              ></span>
            </div>
          </div>
          <div
            v-show="total_pics != 0"
            class="grid-content bg-purple"
            id="three"
            ref="three"
          >
            <div id="threeTag" ref="threeTag"></div>
            <img
              id="biaoZu"
              :src="imgPath"
              alt=""
              @load="resizeImgV2(curImage)"
              ref="biaoZu"
            />
            <canvas id="myCanvas" ref="myCanvas"></canvas>
          </div>
          <div class="info">
            <span>title</span>
            <span
              ><el-button
                size="mini"
                round
                @click="up"
                v-show="curImageIndex != 0"
                ></el-button
              ><el-button
                size="mini"
                round
                @click="down"
                v-show="curImageIndex != isTagImgList.length - 1"
                ></el-button
              ></span
            >
          </div>
        </div>
      </el-col>
      <!--  -->
      <!-- class="labels" -->
      <el-col :span="4">
        <div id="labelManage">
          <!--  -->
          <div class="labelsList">
            <el-row class="title"></el-row>
            <el-row class="searchtag">
              <el-col :span="21">
                <el-input
                  class="searchs"
                  ref="inlineInput"
                  type="text"
                  v-model="search"
                  placeholder=""
                  prefix-icon="el-icon-search"
                  clearable
                  @clear="clearList()"
                  @change="searchAction()"
                  @focus="handleSearch()"
                  @blur="clear()"
                >
                </el-input>
              </el-col>
              <el-col :span="3">
                <el-button class="addtag" size="small" @click="managerTag()"
                  >+</el-button
                >
              </el-col>
            </el-row>
            <el-row>
              <el-scrollbar ref="scrollList" class="scrollList">
                <ul
                  v-infinite-scroll="load"
                  :infinite-scroll-disabled="disabled"
                  :infinite-scroll-immediate="false"
                  :infinite-scroll-delay="500"
                  class="labelList"
                  v-for="(item, index) in dynamicTags"
                  :key="item + index"
                >
                  <li class="labelitem">{{ item }}</li>
                </ul>
              </el-scrollbar>
            </el-row>
            <el-row class="page">
              <el-pagination small layout="prev, pager, next" :total="50">
              </el-pagination>
            </el-row>
          </div>
          <div class="labelRecord">
            <el-row class="title"></el-row>

            <el-row v-if="isTaged" class="labelList">
              <!-- <el-col
                class="labelitem"
                v-for="(item, index) in dynamicTags"
                :key="item + index"
                >{{ item }}</el-col
              > -->
              <el-table
                :data="tableList"
                class="labels"
                max-height="220px"
                max-width="160px"
                :row-style="{ height: '34px' }"
                :cell-style="{ padding: '0' }"
              >
                <el-table-column
                  prop="date"
                  label=""
                  width="90"
                ></el-table-column>
                <el-table-column
                  prop="name"
                  label=""
                  width="50"
                ></el-table-column>
              </el-table>
            </el-row>
            <el-row v-if="!isTaged"></el-row>
          </div>
        </div>
        <!--  -->
      </el-col>
    </el-row>
  </div>
</template>

<script>
import jcanvas from "jcanvas";
jcanvas($, window);

export default {
  data() {
    return {
      // 
      // imgPathList: [],
      //
      // ImgPathListed: [],
      //  
      // imgPathListIndex: 0,
      //
      // imgTagIndex: 0,
      //
      imgPath: "",
      //
      imgWH: { width: 0, height: 0 },
      //, false
      looking: false,
      tagImg: [],
      // :
      msgList: [],
      //
      tagName: "",
      trained: 10,
      untrained: 20,
      //
      layer: 0,

      // : 
      imgTagDist: {
        folder: "",
        version: "",
        imgName: "",
        imgWidth: "",
        imgHeight: "",
        TagList: [{ tagName: "", leftUp: [], rightDown: [] }],
        ratio: "",
        action: "",
      },
      // 
      dialogVisible: false,
      //
      restaurants: [],
      // 
      ratio: 1,
      //
      dialogVisible2: false,
      dynamicTags: [],
      inputVisible: false,
      inputValue: "",

      // pageShow: {
      //   version: "",
      //   modelName: "",
      //   trained: "",
      //   untrained: "",
      // },
      //,isLoading2 
      isLoading: 0,
      loading: "",
      /**
       * 
       */
      //    [{ imagename:,image:,imagesize:"," }]
      isTagImgList: [],
      pagenumber: 1, //
      pagesize: 12, //
      total_pics: 0, //
      total_pages: 1, //
      isTaged: false, // ,
      curImage: {}, //
      ratioHeight: 680, //
      ratioWidth: 1200, //
      curImageIndex: 0,
      // layer: 0,
      //  vuex
      paramList: {
        dataSetName: "", //   
        pagenumber: 1, //
        pagesize: 12,
        tagStatus: 0, // 
        scaleHeight: 240,
      },
      disabled: false,
      tableList: [], //
      currentPage: 1,
      pageSize: 12,
      search: "", //
    };
  },
  computed: {
    //TagImage
    imgUrl() {
      const that = this;
      return that.imgPath;
    },
    // 
    itemDate() {
      return this.$store.state.ItemDetails.itemDate;
    },
    //  ,looking
    twoListLength() {
      //   return [this.ImgPathListed.length, this.imgPathList.length, this.looking]
      return [1, 1, this.looking];
    },
    // isPageShow() {
    //   return this.pageShow;
    // },
    //
    imgParam() {
      let param = {};
      param.isTaged = this.isTaged; //
      param.pageNum = this.pagenumber; //
      console.log(param);
      return param;
    },
    // 
    isNextPage() {
      let isNext = "ok";
      if (
        this.pagenumber * this.pagesize >= this.total_pics &&
        this.pagenumber == 1
      ) {
        isNext = "unAll";
      } else if (this.pagenumber * this.pagesize > this.total_pics) {
        isNext = "unNext";
      } else if (this.pagenumber <= 1) {
        isNext = "unLast";
      }
      return isNext;
    },
  },
  watch: {
    twoListLength: {
      handler(newVal, oldVal) {
        //   [,]
        console.log(newVal);
        if (newVal[0] === 0 && this.looking) {
          console.log(",");
          $(".Empty").css({ display: "flex" });
          $(".noEmpty").css({ display: "none" });
        } else if (newVal[1] === 0 && !this.looking) {
          console.log(",");
          $(".Empty").css({ display: "flex" });
          $(".noEmpty").css({ display: "none" });
        } else if (newVal[0] !== 0 && this.looking) {
          $(".Empty").css({ display: "none" });
          $(".noEmpty").css({ display: "table" });
          this.drawPen();
        } else if (newVal[1] !== 0 && !this.looking) {
          $(".Empty").css({ display: "none" });
          $(".noEmpty").css({ display: "table" });
          this.drawPen();
        }
      },
      deep: true,
    },
    // isPageShow: {
    //   handler(newVal, oldVal) {
    //     this.$store.commit("setImgNum", newVal.trained);
    //   },
    //   deep: true,
    // },
    //isloading2,
    isLoading: {
      handler(newVal, oldVal) {
        if (newVal == 2) {
          this.loading.close();
        }
      },
    },
    // 
    imgParam: {
      handler(newVal) {
        // this.createLoading();
        console.log(this.pagenumber);
        this.paramList.pageNum = this.pagenumber;
        if (newVal.isTaged) {
          //
          this.getTagedImage();
        } else {
          //
          this.getUnTagImage();
          //   this.imgPath = require('assets/images/kong.png')
        }
      },
    },
  },
  methods: {
    up() {
      // if (this.curImageIndex != 0) {
      //  this.isTagImgList[this.curImageIndex-1]
      this.curImageIndex -= 1;
      console.log(this.curImageIndex);
      this.curImage = this.isTagImgList[this.curImageIndex];
      this.resizeImgV2(this.curImage);
      // console.log(this.curImage);
      // }
    },
    down() {
      if (this.curImageIndex < this.isTagImgList.length - 1) {
        //  this.isTagImgList[this.curImageIndex-1]
        this.curImageIndex += 1;
        console.log(this.curImageIndex);
        this.curImage = this.isTagImgList[this.curImageIndex];
        this.resizeImgV2(this.curImage);
      }
    },
    // 
    load() {
      this.getTagName();
    },
    clearList() {},
    searchAction() {},
    handleSearch() {},
    clear() {},
    //  :drawPen()
    resizeWindow() {
      const that = this;
      $(function () {
        console.log("");
        $(window).resize(() => {
          that.stopTag();
          that.startTag();

          //   setTimeout(() => {
          //     that.startTag()
          //   }, 1000)
        });
      });
    },
    //,
    getTagName() {
      const that = this;
      console.log(this.$store.state.ItemDetails.itemDate);
      that.paramList.dataSetName = this.$store.state.ItemDetails.itemDate;
      this.$api
        .showLabel({
          dataSetName: that.paramList.dataSetName,
        })
        .then((res) => {
          console.log(res);
          if (Array.isArray(res.voc_classes) && res.voc_classes) {
            that.restaurants = res.voc_classes;
            this.dynamicTags = [];
            that.restaurants.forEach((res) => {
              that.dynamicTags.push(res.value);
            });
          } else {
            that.$message({
              type: "info",
              message: ",",
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //
    showData() {
      this.paramList.dataSetName = this.$store.state.ItemDetails.itemDate;
      // sessionStorage.setItem(
      //   "store",
      //   JSON.stringify(this.paramList.dataSetName)
      // );
      this.getTagName();
    },
    //
    lastPage() {
      // this.$router.go(-1);
      this.$router.push({ name: "image" });
      // });
      this.isTagImgList[0] = "";
      this.curImage = "";
      this.isTagImgList = [];
      this.msgList = [];
      this.tagImg = [];
      this.tagName = "";
      // this.paramList.dataSetName = "";
      this.clearCanvas();
    },

    // 
    stopTag() {
      $("#myCanvas")[0].onmousedown = undefined;
      $("#myCanvas")[0].onmouseup = undefined;
      $("#myCanvas")[0].onmousemove = undefined;
    },
    //
    startTag() {
      this.stopTag();
      this.drawPen();
    },
    // 
    drawPen() {
      let color = "red";
      let width = 1;
      this.stopTag();
      this.drawRect("myCanvas", color, width);
    },
    // 
    drawRect(canvasId, penColor, strokeWidth) {
      let that = this;
      let penWidth = strokeWidth;
      let result = { leftUp: [], rightDown: [], tagName: "" };
      let canvas = document.getElementById(canvasId);
      //canvas 
      let canvasRect = canvas.getBoundingClientRect();
      //
      let canvasLeft = canvasRect.left;
      let canvasTop = canvasRect.top;
      let x = 0;
      let y = 0;
      let layerName = "layer";
      //
      canvas.onmousedown = function (e) {
        //
        let color = penColor;
        that.layer += 1;
        layerName = `layer${that.layer}`;
        x = e.clientX - canvasLeft;
        y = e.clientY - canvasTop;
        // 
        console.log("");
        console.log(x, y);
        // 
        let x1 = parseInt(x / that.ratio);
        let y1 = parseInt(y / that.ratio);
        result.leftUp = [x1, y1];
        $("#" + canvasId).addLayer({
          type: "rectangle",
          strokeStyle: color,
          strokeWidth: penWidth,
          name: layerName,
          fromCenter: false,
          x: x,
          y: y,
          width: 1,
          height: 1,
        });

        $("#" + canvasId).drawLayers();
        $("#" + canvasId).saveCanvas();
        //
        canvas.onmousemove = function (e) {
          let width = e.clientX - canvasLeft - x;
          let height = e.clientY - canvasTop - y;

          $("#" + canvasId).removeLayer(layerName);

          $("#" + canvasId).addLayer({
            type: "rectangle",
            strokeStyle: color,
            strokeWidth: penWidth,
            name: layerName,
            fromCenter: false,
            x: x,
            y: y,
            width: width,
            height: height,
          });

          $("#" + canvasId).drawLayers();
        };
      };
      //
      canvas.onmouseup = function (e) {
        let color = penColor;
        let penWidth = penWidth;
        canvas.onmousemove = null;
        let width = e.clientX - canvasLeft - x;
        let height = e.clientY - canvasTop - y;

        // 
        let canvasX = parseInt((e.clientX - canvasLeft) / that.ratio);
        let canvasY = parseInt((e.clientY - canvasTop) / that.ratio);
        $("#" + canvasId).removeLayer(layerName);
        if (width > 10 && height > 10) {
          //   0
          //   result.rightDown = [e.clientX - canvasLeft, e.clientY - canvasTop]
          //   
          result.rightDown = [canvasX, canvasY];
          //   
          result.tagName = that.tagName;
          $("#" + canvasId).addLayer({
            type: "rectangle",
            strokeStyle: color,
            strokeWidth: penWidth,
            name: layerName,
            fromCenter: false,
            x: x,
            y: y,
            width: width,
            height: height,
          });
          //   msgList
          that.msgList.push(result);
          console.log(that.msgList);
          that.dialogVisible = true;
          that.createTag(...[x, y]);
        } else {
          that.layer -= 1;
        }
        result = {};
        $("#" + canvasId).drawLayers();
        $("#" + canvasId).saveCanvas();
      };
    },
    //
    createTag(canvasX, canvasY) {
      //   console.log(canvasX, canvasY)
      const that = this;
      let div = $(`<div id="layer${that.layer}"></div>`);
      div.css({
        position: "absolute",
        background: "#99a9bf",
        left: `${canvasX}px`,
        top: `${canvasY}px`,
        color: "yellow",
        "z-index": 1000,
      });
      $("#threeTag").append(div);
    },

    // 
    querySearch(queryString, cb) {
      const that = this;
      let restaurants = that.restaurants;
      console.log(restaurants);
      cb(restaurants);
    },
    // input
    handleSelect(item) {
      console.log(item);
      this.tagImg = item;
    },
    // 
    addLabelBoxes(tagList, curImageIndex) {
      let labelBoxes = [];
      if (!Array.isArray(tagList) || tagList.length <= 0) {
        return;
      }
      tagList.forEach((item, index) => {
        let labItem = {};
        labItem.label = item.tagName;
        labItem.xmin = item.leftUp[0];
        labItem.ymin = item.leftUp[1];
        labItem.xmax = item.rightDown[0];
        labItem.ymax = item.rightDown[1];
        labelBoxes.push(labItem);
      });
      console.log(labelBoxes);
      this.isTagImgList[curImageIndex].labelBoxes = labelBoxes;
      console.log(curImageIndex, this.isTagImgList[curImageIndex].labelBoxes);
      this.isTagImgList[curImageIndex].ratio = this.ratio;
      console.log(this.isTagImgList);
      // this.saveData();
      this.sendTagList();
    },
    //
    sendTagList() {
      const that = this;
      console.log(this.isTagImgList);
      let msg = this.isTagImgList
        .filter((item) => item.isClick && Array.isArray(item.labelBoxes))
        .map((item) => {
          // item.image = "";
          // item.imagename = "";
          return item;
        });

      console.log(msg);
      let imgname = {};
      let b = msg.forEach((item) => {
        imgname = item.imagename;
      });
      console.log(imgname);
      let imgItem = {};
      let a = msg.forEach((item) => {
        imgItem = item;
      });
      console.log(imgItem);
      console.log(imgItem["imagesize"]);

      let imgWidth = parseInt(imgItem["imagesize"].split(",")[0]);
      let imgHeight = parseInt(imgItem["imagesize"].split(",")[1]);

      let params = {};
      params.dataset = this.paramList.dataSetName;
      params.imgName = imgname;
      params.imgWidth = imgWidth;
      params.imgHeight = imgHeight;
      params.TagList = this.msgList;
      params.ratio = this.ratio;
      that.$api
        .createTag(params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 
    cancel() {
      this.removeOne();
      this.dialogVisible = false;
    },
    // 
    determine() {
      const that = this;
      //,
      if (that.tagName == "") {
        return;
      }
      // dom
      $(`#layer${that.layer}`).text(that.tagName);
      //msgList
      that.msgList[that.msgList.length - 1].tagName = that.tagName;
      this.dialogVisible = false;
    },
    // 
    removeOne() {
      const that = this;
      let index = that.layer;
      console.log(that.msgList);
      //msgList
      that.msgList.pop();
      //canvas 
      $("#myCanvas").removeLayer(`layer${index}`);
      //dom
      $(`#layer${index}`).remove();
      $(`#layer${index}`).empty();
      $("#myCanvas").drawLayers();
      $("#myCanvas").saveCanvas();
      that.layer = that.layer === 0 ? 0 : index - 1;
    },
    removeTag(curImage, curImageIndex) {
      console.log(curImage, curImageIndex, this.isTagImgList[curImageIndex]);
      const that = this;
      let index = that.layer;
      console.log(index);
      for (let i = index; i >= 1; i--) {
        //canvas 
        console.log("" + i);
        $("#myCanvas").removeLayer(`layer${i}`);
        //dom
        $(`#layer${i}`).remove();
        $(`#layer${i}`).empty();
        $("#myCanvas").drawLayers();
        $("#myCanvas").saveCanvas();
        that.layer = that.layer === 0 ? 0 : i - 1;
      }
      let imgWidth = parseInt(curImage["imagesize"].split(",")[0]);
      let imgHeight = parseInt(curImage["imagesize"].split(",")[1]);
      let params = {};
      params.dataset = this.paramList.dataSetName;
      params.imgName = curImage.imagename;
      params.imgWidth = imgWidth;
      params.imgHeight = imgHeight;
      params.ratio = this.ratio;
      this.$api
        .deleteTag(params)
        .then((res) => {
          console.log(res);
          // console.log(curImageIndex);
          console.log(that.isTagImgList[curImageIndex].labelBoxes);
          delete that.isTagImgList[curImageIndex].labelBoxes;
          that.isTagImgList[curImageIndex].del = true;
          console.log(that.isTagImgList[curImageIndex]);
          that.stopTag();
        })
        .catch((err) => {
          console.log(err);
        });
      // if (curImage.isClick && that.isTagImgList[curImageIndex].del === true) {
      //   this.stopTag();
      // }
    },
    //,
    clearAll() {
      const that = this;
      that.msgList = [];
      //
      $("#threeTag").empty();
      let index = that.layer;
      that.layer = 0;
      that.imgTagDist = {};
      that.ratio = 1;
      //canvas 
      while (index > 0) {
        $("#myCanvas").removeLayer(`layer${index}`);
        $("#myCanvas").drawLayers();
        $("#myCanvas").saveCanvas();
        index -= 1;
      }
      $("#myCanvas").drawLayers();
      $("#myCanvas").saveCanvas();
    },
    //
    imgRatio(imgUrl) {
      if (typeof imgUrl == "string") {
        console.log(imgUrl);
        let img = new Image();
        img.src = imgUrl;
        //
        img.onload = () => {
          that.imgWH.width = img.width;
          that.imgWH.height = img.height;
          let ratio = 480 / img.height;
          that.ratio = ratio;
          let width = parseInt(ratio * img.width);
          let height = parseInt(ratio * img.height);
          $("#biaoZu").css({
            height: `${height}px`,
            width: `${width}px`,
          });
          that.imgPath = imgUrl;
        };
      }
      const that = this;
    },
    //
    managerTag() {
      this.dialogVisible2 = true;
      const that = this;
      this.dynamicTags = [];
      that.restaurants.forEach((res) => {
        that.dynamicTags.push(res.value);
      });
      console.log("");
    },
    // 
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
    },
    // input
    showInput() {
      this.inputVisible = true;
      this.$nextTick((_) => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    // input
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.dynamicTags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = "";
    },
    // 
    cancelManage() {
      this.dialogVisible2 = false;
    },
    // 
    determineManage() {
      this.dialogVisible2 = false;
      this.restaurants = [];
      this.dynamicTags.forEach((item) => {
        this.restaurants.push({ value: item });
      });
      // 
      this.postTagList();
    },
    //
    postTagList() {
      const that = this;
      that.$api
        .uploadLabel({
          dataSetName: this.paramList.dataSetName,
          voc_classes: this.restaurants,
        })
        .then((res) => {
          console.log(res);
          // that.restaurants = res.voc_classes;
          that.$message({
            type: "success",
            message: "",
          });
        })
        .catch((error) => {
          console.log(error);
          that.$message({
            type: "error",
            message: "",
          });
        });
    },
    //
    unTarget() {
      this.isTagImgList[this.curImageIndex].labelBoxes = [];
      this.isTagImgList[this.curImageIndex].unTag = true;
      this.isTagImgList[this.curImageIndex].ratio = 1;
      // this.isTagImgList[this.curImageIndex].tagText = "";
      this.sendTagList();
    },
    // 
    saveData() {
      this.addLabelBoxes(this.msgList, this.curImageIndex);
      // this.isTagImgList[this.curImageIndex].labelBoxes ;
      this.stopTag();
    },

    /*   */

    //
    createLoading() {
      this.loading = this.$loading({
        lock: true,
        text: "...",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
      });
    },
    //  
    getUnTagImage() {
      console.log("");
      const that = this;
      that.curImageIndex = 0;
      // this.paramList.dataSetName = this.itemDate;
      this.paramList.dataSetName = this.$store.state.ItemDetails.itemDate;
      this.$store.commit(
        "ItemDetails/unTagImgList",
        this.paramList.dataSetName
      );
      // console.log(this.itemDate);
      // this.$store.commit("ItemDetails/paramList", that.paramList);
      // sessionStorage.getItem("store");

      this.$api
        .ImageList({
          dataSetName: this.paramList.dataSetName,
          pageNum: this.paramList.pagenumber,
          pageSize: this.paramList.pagesize,
          tagStatus: this.paramList.tagStatus,
          scaleHeight: this.paramList.scaleHeight,
        })
        .then((res) => {
          console.log(res);
          console.log(this.paramList.dataSetName);
          this.$store.commit("ItemDetails/unTagImgList", this.paramList);
          if (res.code == 200) {
            that.total_pics = res.images.total_pics;
            that.total_pages = res.images.total_pages;
            that.isTagImgList = res.images.imgs;
            // this.$store.commit("ItemDetails/isTagImgList", that.isTagImgList);
            if (that.isTagImgList.length > 0) {
              that.isTagImgList[0].isClick = true;
              that.curImage = that.isTagImgList[0];
              // this.$store.commit("ItemDetails/curImage", that.curImage);
              // console.log(that.curImage);

              that.resizeImgV2(that.isTagImgList[0]);

              that.imgPath = that.isTagImgList[0].image;
              // 
              // that.startTag();
            }
          } else if (res.code == 402) {
            this.total_pics = res.image_msg.length;
            that.$message({
              type: "",
              message: "",
            });
          } else {
            that.$message({
              type: "error",
              message: "",
            });
          }
          // that.loading.close();
        })
        .catch((err) => {
          // that.loading.close();
          console.log(err);
        });
    },
    //  
    getTagedImage() {
      const that = this;
      that.curImageIndex = 0;
      this.$api
        .ImageList({
          dataSetName: this.paramList.dataSetName,
          pageNum: this.paramList.pagenumber,
          pageSize: this.paramList.pagesize,
          tagStatus: this.paramList.tagStatus,
          scaleHeight: this.paramList.scaleHeight,
        })
        .then((res) => {
          if (res.code == 200) {
            this.$store.commit("ItemDetails/isTagImgList", this.paramList);
            console.log(res);
            that.total_pics = res.images.total_pics;
            that.total_pages = res.images.total_pages;
            that.isTagImgList = res.images.imgs;

            if (that.isTagImgList.length > 0) {
              // that.startTag();
              that.isTagImgList[0].isClick = true;
              that.curImage = that.isTagImgList[0];
              // this.$store.commit("ItemDetails/curImage", that.curImage);
              // console.log(that.curImage);
              that.resizeImgV2(that.isTagImgList[0]);
              console.log(that.isTagImgList[0]);
              that.imgPath = that.isTagImgList[0].image;
            }
            //   console.log();
          } else if (res.code == 402) {
            this.total_pics = res.image_msg.length;
            that.$message({
              type: "",
              message: "",
            });
          } else {
            that.$message({
              type: "error",
              message: "",
            });
          }
          // that.loading.close();
        })
        .catch((err) => {
          console.log(err);
          // that.loading.close();
        });
    },
    //
    setIsTaged(tf) {
      console.log(tf);
      // false ,true 

      // 
      this.pagenumber = 1;
      this.paramList.pagenumber = 1;
      this.isTaged = tf;
      if (this.isTaged === false) {
        this.paramList.tagStatus = 0;
      } else {
        this.paramList.tagStatus = 1;
      }

      console.log("" + tf);

      //   
      this.addLabelBoxes(this.msgList, this.curImageIndex);
      $("#threeTag").empty();
      // 
      this.clearCanvas();
      // 
      this.msgList = [];
    },
    //  ,  
    getPage(num) {
      //   
      // this.sendTagList();
      this.pagenumber += num;
      this.paramList.pagenumber = this.pagenumber;
      if (this.isTaged) {
        this.getTagName();
      } else {
        this.getUnTagImage();
      }
    },
    // 
    selectImg(e) {
      this.addLabelBoxes(this.msgList, this.curImageIndex);
      this.msgList = [];
      this.curImage = this.isTagImgList[e.index];
      //   
      this.isTagImgList[e.index].isClick = true;
      this.curImageIndex = e.index;
      console.log(e.index);
      this.resizeImgV2(this.curImage);
      console.log(this.curImage);
    },
    // imgItem
    resizeImgV2(imgItem) {
      const that = this;
      console.log(this.curImage);
      // 
      $("#threeTag").empty();
      that.clearCanvas();

      let imgHeight = parseInt(imgItem["imagesize"].split(",")[1]);
      let imgWidth = parseInt(imgItem["imagesize"].split(",")[0]);

      console.log(imgHeight, imgWidth);
      let ratio = "";
      let width = "";
      let height = "";
      ratio = that.ratioWidth / imgWidth;
      that.ratio = ratio;
      if (ratio < 0.8) {
        ratio = that.ratioWidth / imgWidth;
        that.ratio = ratio;
        console.log("" + ratio);
        width = parseInt(ratio * imgWidth);
        height = parseInt(that.ratio * imgHeight);
      } else if (ratio > 0.8) {
        ratio = that.ratioHeight / imgHeight;
        that.ratio = ratio;
        console.log("" + ratio);
        width = parseInt(ratio * imgWidth);
        height = parseInt(ratio * imgHeight);
      }
      // let height = parseInt(that.ratio * imgHeight);
      // let height = parseInt(ratio * imgHeight);
      //
      this.$refs["biaoZu"].style.height = `${height}px`;
      this.$refs["biaoZu"].style.width = `${width}px`;
      // 
      this.$refs["myCanvas"].setAttribute("width", `${width}px`);
      this.$refs["myCanvas"].setAttribute("height", `${height}px`);
      this.$refs["threeTag"].style.height = `${height}px`;
      this.$refs["threeTag"].style.width = `${width}px`;
      this.imgPath = imgItem.image;
      // that.stopTag();
      // that.drawPen();
      that.startTag();
      if (Array.isArray(imgItem["labelBoxes"])) {
        that.layer = imgItem["labelBoxes"].length;
        //  
        this.stopTag();
        this.addTagRect(imgItem["labelBoxes"]);
      } else if (imgItem["del"] === true) {
        this.stopTag();
      }
    },
    // 
    addTagRect(labelBoxes) {
      const that = this;
      let ratio = this.ratio;
      that.layer = 0;
      //
      labelBoxes.forEach((item, index) => {
        let x = parseInt(item.xmin * ratio);
        let y = parseInt(item.ymin * ratio);
        let width = parseInt((item.xmax - item.xmin) * ratio);
        let height = parseInt((item.ymax - item.ymin) * ratio);
        let tagName = item.label;
        that.layer += 1;
        // 
        let div = $(
          `<div id="layer${that.layer}" class="isShowLayer">${tagName}</div>`
        );
        div.css({
          position: "absolute",
          background: "rgb(107,124,152)",
          left: `${x}px`,
          top: `${y}px`,
          color: "yellow",
          "z-index": 1000,
        });
        // 
        $("#myCanvas").addLayer({
          type: "rectangle",
          strokeStyle: "red",
          strokeWidth: 1,
          name: `layer${that.layer}`,
          fromCenter: false,
          x: x,
          y: y,
          width: width,
          height: height,
        });
        $("#myCanvas").drawLayers();
        $("#myCanvas").saveCanvas();
        $("#threeTag").append(div);
      });
    },
    // 
    clearCanvas() {
      const that = this;
      //
      //   $('#threeTag').empty()
      let index = that.layer;
      that.layer = 0;
      that.imgTagDist = {};
      that.ratio = 1;
      //canvas 
      while (index > 0) {
        $("#myCanvas").removeLayer(`layer${index}`);
        $("#myCanvas").drawLayers();
        $("#myCanvas").saveCanvas();
        index -= 1;
      }
      $("#myCanvas").drawLayers();
      $("#myCanvas").saveCanvas();
    },
  },

  created() {
    // this.getUnTagImage();
  },
  mounted() {
    //
    this.resizeWindow();
    this.getUnTagImage();
    this.getTagName();
  },
  activated() {
    this.getUnTagImage();
    this.getTagName();
  },
  beforeDestroy() {
    this.cancelListening();
  },
};
</script>

<style lang="scss" scoped>
@import "~assets/scss/Navigation/Details/tagImage.scss";
</style>
